@charset "utf-8";

/* ────────────────────────────────────────────────────────── */
/* 1) 배너 컨테이너 & 이미지 높이 고정 */
.upcon_banner {
  position: relative;
  width: 100%;
  height: 137px;        /* 배너 높이 지정 */
  overflow: hidden;
}
.upcon_banner img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 137px;        /* 강제 고정 */
  object-fit: cover;    /* 비율 유지는 하되 잘릴 수 있음 */
  opacity: 0.7;
}

/* ────────────────────────────────────────────────────────── */
/* 2) 오버레이: 배너 위 전체 덮기 + flex 중앙 정렬 */
.banner-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 137px;        /* 배너와 동일 높이 */
  display: flex;
  align-items: center;  /* Y축 중앙 */
  overflow: hidden;
  pointer-events: none; /* 클릭 방해 안 함 */
}

/* ────────────────────────────────────────────────────────── */
/* 3) 슬라이더 트랙: ABAB 반복 */
.slider-track {
  display: inline-flex;
  white-space: nowrap;
  animation: marquee 20s linear infinite;
}

/* ────────────────────────────────────────────────────────── */
/* 4) 각 덩어리 항목 */
.slider-item {
  flex: 0 0 auto;
  text-align: center;
  margin: 0 40px;      /* 덩어리 사이 간격 */
}

/* ────────────────────────────────────────────────────────── */
/* 5) 텍스트 크기 조정 */
.banner-title {
  margin: 0;
  font-size: 40px;     /* 큰 글씨 */
  font-weight: bold;
  color: #1fa5ff;
  /* text-shadow: 1px 1px 4px rgba(0,0,0,0.6); */
  line-height: 1.2;
}
.banner-date {
  margin: 4px 0 0;
  font-size: 20px;     /* 작은 글씨 */
  color: #1fa5ff;
  /* text-shadow: 1px 1px 3px rgba(0,0,0,0.5); */
}

/* ────────────────────────────────────────────────────────── */
/* 6) 애니메이션: 전체 트랙의 절반만큼 이동 */
@keyframes marquee {
  0%   { transform: translateX(0); }
  100% { transform: translateX(-50%); }
}

/* ────────────────────────────────────────────────────────── */
/* 7) 메뉴박스 기존 스타일 유지 */
.upcon_menubox {
  width: 1062px;
  margin: 100px auto;
}
.upcon_menubox ul {
  display: flex;
  justify-content: space-evenly;
}
.upcon_menubox ul li p {
  text-align: center;
  margin-top: 10px;
}
.upcon_menuimg img {
  border-radius: 36px;
  transition: 0.3s;
}
.upcon_menuimg img:hover {
  box-shadow: 0 0 20px rgba(0,0,0,0.5);
}
